@import url("https://fonts.googleapis.com/css2?family=Lato:ital,wght@0,100;0,300;0,400;0,700;0,900;1,100;1,300;1,400;1,700;1,900&display=swap");

body {
  background-color: black;
  overflow-x:hidden ;
}
.container {
  position: absolute;
  height: 690px;
  width: 400px;
  margin-left: 40%;
  margin-top: 2%;
  border-radius: 15px;
  background-image: url("./phone.png");
  background-size: cover;
  background-position: -50px;
}
.blink {
  width: 80vw;
  position: absolute;
  margin-left: 8%;
  margin-right: 8%;
  overflow: auto;
  top: 30%;
  color: white;
  font-size: 300%;
  font-family: "Lato";
  font-weight: 300;
}
.btn {
  top: 40%;
  left: 8%;
  position: absolute;
  font-family: "Lato";
  font-weight: 400;
  font-size: small;
  height: 50%;
  width: 296px;
  /* border: 2px solid white; */
}
.btns {
  height: 18%;
  width: 21%;
  border-radius: 100px;
  border: none;
  color: white;
  z-index: -3;
}
.ac {
  background-color: rgb(222, 222, 222);
  margin-left: 1.5%;
  transition: 0.8s;
  color: black;
}
.add {
  background-color: rgb(222, 222, 222);
  margin-left: 4%;
  transition: 0.8s;
  color: black;
}
.moduls {
  background-color: rgb(222, 222, 222);
  margin-left: 4%;
  transition: 0.8s;
  color: black;
}
.divide {
  background-color: rgb(255, 132, 0);
  margin-left: 4%;
  transition: 0.8s;
}
.seven {
  background-color: rgba(222, 222, 222, 0.374);
  margin-left: 1.5%;
  margin-top: 4%;
  transition: 0.8s;
}
.eight {
  background-color: rgba(222, 222, 222, 0.374);
  margin-left: 4%;
  margin-top: 4%;
  transition: 0.8s;
}
.nine {
  background-color: rgba(222, 222, 222, 0.374);
  margin-left: 4%;
  margin-top: 4%;
  transition: 0.8s;
}
.multiply {
  background-color: rgb(255, 132, 0);
  margin-left: 4%;
  margin-top: 4%;
  transition: 0.8s;
}
.ac:hover,
.moduls:hover,
.add:hover {
  background-color: rgb(255, 255, 255);
}
.multiply:hover,
.divide:hover,
.minus:hover,
.equal:hover,
.plus:hover {
  background-color: rgb(255, 162, 0);
}
.four {
  background-color: rgba(222, 222, 222, 0.374);
  margin-left: 1.5%;
  margin-top: 4%;
  transition: 0.6s;
}
.five {
  background-color: rgba(222, 222, 222, 0.374);

  margin-left: 4%;
  margin-top: 4%;
  transition: 0.8s;
}
.six {
  background-color: rgba(222, 222, 222, 0.374);

  margin-left: 4%;
  margin-top: 4%;
  transition: 0.8s;
}
.minus {
  background-color: rgb(255, 132, 0);
  margin-left: 4%;
  margin-top: 4%;
  transition: 0.8s;
}
.one {
  background-color: rgba(222, 222, 222, 0.374);
  margin-left: 1.5%;
  margin-top: 4%;
  transition: 0.8s;
}
.two {
  background-color: rgba(222, 222, 222, 0.374);

  margin-left: 4%;
  margin-top: 4%;
  transition: 0.8s;
}
.three {
  background-color: rgba(222, 222, 222, 0.374);

  margin-left: 4%;
  margin-top: 4%;
  transition: 0.8s;
}
.plus {
  background-color: rgb(255, 132, 0);
  margin-left: 4%;
  margin-top: 4%;
  transition: 0.8s;
}
.zero {
  background-color: rgba(222, 222, 222, 0.374);
  margin-left: 1.5%;
  margin-top: 4%;
  width: 46%;
  transition: 0.8s;
}
.dot {
  background-color: rgba(222, 222, 222, 0.374);
  margin-top: 4%;
  margin-left: 4%;
  transition: 0.8s;
}
.equal {
  background-color: rgba(222, 222, 222, 0.374);
  margin-top: 4%;
  margin-left: 4%;
  background-color: rgb(255, 132, 0);
}
.one:hover,
.two:hover,
.three:hover,
.four:hover,
.five:hover,
.six:hover,
.seven:hover,
.eight:hover,
.nine:hover,
.zero:hover,
.dot:hover {
  background-color: rgba(222, 222, 222, 0.667);
}
